}

.top {
  height: 55px;
  width: 350px;
  margin-bottom: 40px;
}

.middle {
  height: 520px;
  width: 350px;
}

.bottom {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  height: 70px;
  width: 350px;
}

.logo2 {
  height: 45px;
}

